
.button {
      width: 15rem;
      height: 3.75rem;
      filter: drop-shadow(0 0.25rem 0.25rem rgba(0, 0, 0, 0.25));
      border: 0.031rem solid #2d7c73;
      border-radius: 1.875rem;
      font-family: 'FONTSPRING DEMO - Lufga SemiBold';
      font-style: normal;
      font-weight: 600;
      font-size: 1.5rem;
      /*line-height: 1.938rem;*/
      align-items: center;
      text-align: center;
      letter-spacing: 0.08rem;
      background: #2D7C73;
      color:  #FFFFFF;
      margin: 0.5rem;
  }

.button:hover {
      background: #296A62;
}

.input {
      width: 14.5rem;
      height: 3.75rem;
      margin: 0.5rem;
      border-radius: 1.875rem;
      font-family: 'Quicksand';
      font-style: normal;
      font-weight: 400;
      font-size: 1.375rem;
      display: flex;
      align-items: center;
      text-align: center;
      color: #ADADAD;
}